import React from 'react';
import CardsProfessional from './CardsProfessional.js';
import PersonalContainer from './PersonalContainer.js';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

function Container(props) {

    const { ref, inView } = useInView({
        threshold: 1
    });

    return (
        <div className="bodyContainer">
            <motion.div ref={ref} className="containerContent">
                <motion.h1 initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ duration: .75 }} className="containerTitle">CAREER WORK HISTORY</motion.h1>
                <motion.p initial={{  opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ delay: 0.8, duration: 1, ease: "easeOut" }}>As a <strong>designer &amp; user interface developer</strong> at <strong>SAP</strong>, I contribute to the development of the <strong>Fiori Elements</strong> floorplans built using the <strong>SAP UI5</strong> framework.</motion.p>
            </motion.div>
            <CardsProfessional />
            <PersonalContainer />        
        </div>
    );
}

export default Container;