import React from 'react';
import NavHeader from './NavHeader.js';
import Header from './Header.js';
import Container from './Container.js';
import Footer from './Footer.js';
import About from './About.js';
import { Switch, Route, useLocation } from 'react-router-dom';
import { AnimatePresence, motion } from 'framer-motion';
import AboutBackground from '../images/code1.gif';

var oBgImageAbout = {
    backgroundImage: `url(${AboutBackground})`
};

const containerVariants = {
  exit: {
    x: "-100vh",
    transition: { duration: 5, ease: 'easeInOut' }
  }
};

function App(props) {
  const location = useLocation();
  return (
      <div id="wrapper">
        <AnimatePresence>
          <Switch location={location} key={location.key}>
            <Route path="/home" render={() => (
              <motion.div variants={containerVariants} exit="exit" className="homePageContainer">
                <div className="homeHeaderContainer">
                    <nav className="AppNavHeader">
                      <NavHeader />
                    </nav>
                    <header className="AppHeader">
                      <Header />
                    </header>
                  </div>
                <div className="AppContainer">
                  <Container />
                </div>
              </motion.div>
            )} />
            <Route path="/about" render={() => (
              <div>
                <div className="aboutPageContainer" style={ oBgImageAbout }></div>
                <div className="aboutPageBackground">
                    <nav className="AppNavHeader">
                      <NavHeader />
                    </nav>
                    <About />
                </div>
              </div>
            )} />
            <Route path="/" render={() => (
              <motion.div variants={containerVariants} exit="exit" className="homePageContainer">
                <div className="homeHeaderContainer">
                  <nav className="AppNavHeader">
                    <NavHeader />
                  </nav>
                  <header className="AppHeader">
                    <Header />
                  </header>
                </div>
                <div className="AppContainer">
                  <Container />
                </div>
              </motion.div>
            )} />
          </Switch>
        </AnimatePresence>
        <footer className="AppFooter">
          <Footer />
        </footer>
      </div>
  );
}

export default App;