import React from 'react';
import { motion } from 'framer-motion';

const headerNavContainerVariants = {
    hidden: {
        y: -100
    },
    visible: {
        y: 0,
        transition: {
            delay: 1,
            type: 'spring',
            stiffness: 120
        }
    }
}

class NavHeader extends React.Component {

    render() {

        return (
            <div className="headerNavContainer">
                <motion.div className="logo" variants={headerNavContainerVariants} initial="hidden" animate="visible">
                    <a href="/" className="span--portfolioTitle">Rahul Akarapu <motion.span whileHover={{ scale: 1.5, originX: 0 }} transition={{type: 'spring', stiffness: 100 }} className="span--portfolio">portfolio</motion.span></a>
                </motion.div>
                <motion.div className="navigation" variants={headerNavContainerVariants} initial="hidden" animate="visible">
                    <ul className="list--navigation">
                        {/* <Link to="/about">New About</Link> */}
                        <li className="listItem--navBar"><motion.a whileHover={{ fontSize: '32px'}} transition={{type: 'spring', stiffness: 100 }} className="anchor--navBar" href="#/about">about</motion.a></li>
                    </ul>
                </motion.div>
            </div>
        );
    }
}

export default NavHeader;