import React from 'react';
import LRBackground from '../images/ListReport.jpg';
import OPBackground from '../images/ObjectPage.jpg';
import WLBackground from '../images/Worklist.jpg';
import OVPBackground from '../images/OverviewPage.jpg';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

function CardsProfessional(props) {
    const { ref, inView } = useInView({
        threshold: .5
    });

    return (
        <motion.div ref={ref} className="cardContainer">
            <motion.div initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ delay: 0, duration: 0.5 }} className="card" onClick={()=>window.location.href='https://experience.sap.com/fiori-design-web/list-report-floorplan-sap-fiori-element/'}>
                <img className="card__img" src={ LRBackground } width="400px" height="300px" alt=""/>
                <div className="card__text">
                    <h3 className="card__title">List Report</h3>
                </div>
            </motion.div>
            <motion.div initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ delay: 0.5, duration: 0.5 }} className="card" onClick={()=>window.location.href='https://experience.sap.com/fiori-design-web/object-page/'}>
                <img className="card__img" src={ OPBackground } width="400px" height="300px" alt=""/>
                <div className="card__text">
                    <h3 className="card__title">Object Page</h3>
                </div>
            </motion.div>
            <motion.div initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ delay: 1, duration: 0.5 }} className="card" onClick={()=>window.location.href='https://experience.sap.com/fiori-design-web/work-list/'}>
                <img className="card__img" src={ WLBackground } width="400px" height="300px" alt=""/>
                <div className="card__text">
                    <h3 className="card__title">Worklist</h3>
                </div>
            </motion.div>
            <motion.div initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ delay: 1.5, duration: 0.5 }} className="card" onClick={()=>window.location.href='https://experience.sap.com/fiori-design-web/overview-page/'}>
                <img className="card__img" src={ OVPBackground } width="400px" height="300px" alt=""/>
                <div className="card__text">
                    <h3 className="card__title">Overview Page</h3>
                </div>
            </motion.div>                       
        </motion.div>
    );
}

export default CardsProfessional;