import React from 'react';
import CardsPersonal from './CardsPersonal.js';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';

function Container(props) {

    const { ref, inView } = useInView({
        threshold: 1
    });

    return (
        <div>
            <motion.div ref={ref} className="containerContentPersonal">
                <motion.h1 initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ duration: .75 }} className="containerTitle">PERSONAL DEVELOPMENT</motion.h1>
                <motion.p initial={{ opacity: 0 }} animate={ inView ? { opacity: 1 } : ''} transition={{ delay: 0.8, duration: 1, ease: "easeOut" }}>As a <strong>passionate UI Developer</strong>, here are some projects I developed out of <strong>love for coding</strong>.</motion.p>
            </motion.div>
            <CardsPersonal /> 
        </div>
    );
}

export default Container;